import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { Navbar, Button, DropdownButton, Dropdown, OverlayTrigger, Popover } from 'react-bootstrap'
// import Navbar from 'react-bootstrap/Navbar';
// import Button from 'react-bootstrap/Button';
// import DropdownButton from 'react-bootstrap/DropdownButton';
import { withRouter } from 'react-router-dom';
import {linkToSwaggerUri} from '../../constants/api';


const NavigationBar = props => {


    const load = (index) => {
        props.loadContract(index);
    }

    return (
        <Navbar variant="light" expand="md" style={{ backgroundColor: 'white' }}>

            {props.location.pathname === '/profile' &&
                (<div>
                    <Link to="/">
                        <img
                            className={'mr-3'}
                            src={require('../../assets/images/left.png')}
                            height="30"
                            alt="Zenroom logo"
                        />
                    </Link>
                </div>)
            }
            <Navbar.Brand className={'mr-auto'}>
                <Link to="/">
                    <img
                        src={require('../../assets/images/logo.png')}
                        width="150"
                        height="30"
                        className="d-inline-block align-top"
                        alt="Zenroom logo"
                    />
                </Link>
            </Navbar.Brand>

            {/* buttons for main page */}
            {props.location.pathname === '/' &&
                <DropdownButton className={'mr-5'} id="dropdown-variants-Secondary" title="Examples" variant='info'>
                    {props.contracts.map((contract, index) => {
                        return (
                            <Dropdown.Item key={index} onClick={() => { load(index) }}> {contract.name}</Dropdown.Item>
                        );
                    })
                    }

                </DropdownButton>
            }
            {props.location.pathname === '/' && <Button variant="outline-success" className="mr-sm-2" onClick={props.executeZenroom}>Play</Button>}
            {
                !props.isAuthenticated
                    ? <Button variant="outline-primary" className="mr-sm-2" onClick={props.showLogin}>Login</Button>
                    : props.location.pathname === '/'
                        ? (<div>
                            {props.userLoaded
                                ? (
                                    <Fragment>
                                        <Button variant="outline-success" className="mr-sm-2" onClick={props.updateContract}>Update: {props.contractName}</Button>
                                        {/* <Link to="/profile"><Button variant="outline-success" className="mr-sm-2" onClick={()=>{}}>Back</Button></Link> */}
                                        <Link to="/profile">
                                            <img
                                                className={'ml-3'}
                                                src={require('../../assets/images/right.png')}
                                                height="30"
                                                alt="Zenroom logo"
                                            />
                                        </Link>
                                    </Fragment>
                                )
                                : (
                                    <Fragment>
                                        <Button variant="outline-success" className="mr-sm-2" onClick={props.showSave}>Save</Button>
                                        < OverlayTrigger
                                            trigger="click"
                                            key={'bottom'}
                                            placement={'bottom'}
                                            overlay={
                                                <Popover id={'popover-positioned-bottom'}>
                                                    <Popover.Content style={{ padding: 0 }}>
                                                        <div className={'list-group'}>
                                                            <Link to="/profile" className={'list-group-item list-group-item-action'}>
                                                                My Contracts
                                                            </Link>

                                                            <a style={{ cursor: 'pointer' }} className={'list-group-item list-group-item-action'} onClick={() => props.logout()}><strong>Sign Out</strong></a>
                                                        </div>
                                                    </Popover.Content>
                                                </Popover>
                                            }
                                        >
                                            {/* <Button variant="secondary">Icon</Button> */}
                                            <img
                                                style={{ cursor: 'pointer' }}
                                                src={require('../../assets/images/user.png')}
                                                width="auto"
                                                height="40"
                                                // className="d-inline-block align-top"
                                                alt="Zenroom logo"
                                            />
                                        </OverlayTrigger>
                                    </Fragment>)
                            }
                        </div>)
                        : null
            }

            {/* buttons for profile page */}
            {props.location.pathname === '/profile' && (
                <Fragment>
                    <Button variant="outline-success" className="mr-sm-2">Export</Button>
                    <Link to="/docs"><Button variant="outline-success" className="mr-sm-2" onClick={() =>  window.open(linkToSwaggerUri, "_blank")}>Swagger</Button></Link>
                </Fragment>
            )}
        </Navbar >
    );
}

export default withRouter(NavigationBar);